h2,
ul,
li {
  margin: 0;
  padding: 0;
}

.title {
  display: block;
  width: 310px;
  margin: 0 auto;
  padding: 10px 0;
  text-align: center;
  background-color: white;
}

.statList {
  display: flex;
  flex-direction: row;
  flex: 1 0 100%;
  text-align: center;
  justify-content: center;
  width: 250px;
  margin: 0 auto;
  list-style: none;
  color: white;
}

.item {
  display: flex;
  flex-direction: column;
  /* font-size: 34px; */
  flex: 1 0 20%;
  background-color: #4ec2f7;
  padding: 10px 5px;
  width: 250px;
  border: 1px solid black;
}

.label {
  color: black;
  font-size: 14px;
  margin-bottom: 10px;
}

.percentage {
  color: black;

  font-size: 20px;
}
