.itemFriends {
  width: 180px;
  border: solid 3px rgba(1, 1, 1, 0.011);
  border-radius: 4px;
  list-style: none;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  background-color: #fff;
  box-shadow: 0px 3px 3px 0px rgba(0, 0, 0, 0.75);
  margin-bottom: 10px;
}

.itemFriends:not(:last-child) {
  margin-bottom: 10px;
}

.avatar {
  margin-right: 10px;
  width: 55px;
  border-radius: 5px;
}

.name {
  font-size: 20px;
  font-weight: 700;
}

.online {
  background-color: green;
}

.offline {
  background-color: red;
}

.status {
  margin-right: 10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.itemFriends span {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin-right: 7px;
}
